import React from "react";
import PropTypes from "prop-types";

import bkImage from "assets/img/bg.jpg";
import logo from "assets/img/auth_logo.png";

import Button from "components/CustomButtons/Button.js";

export default function TopCarousel(props) {
  const { isAuthenticated } = props;

  return (
    <div style={{ width: "100%", 
                  backgroundSize: "100% 100%", 
                  backgroundImage: "url(" + bkImage + ")",
                  padding: "150px 10%" }}
    > 
      <img src={logo} alt="..." width="400px"/><br /><br />
      <p style={{ fontSize: "22px", color: "white" }}><b></b></p>
      <p style={{ fontSize: "22px", color: "white" }}><b></b></p>
      { !isAuthenticated && 
        <Button style={{ backgroundColor: "#e12e3e", color: "white", fontSize: "16px", marginTop: "50px" }}>
          <a href="/auth/register" style={{ color: "white" }} rel={"noindex nofollow"}></a>
        </Button> }
    </div>
  );
}

TopCarousel.propTypes = {
  isAuthenticated: PropTypes.bool.isRequired
};
